import { Task } from './../Task';
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import "rxjs/Rx";

@Injectable()
export class TaskService {

  domain : string = "http://localhost:3000";

  constructor(private http:HttpClient) {}

  getTasks(){
    return this.http.get<Task[]>(`${this.domain}/api/tasks`).map(res=>res);
  };
  addTasks(newTask:Task){
    return this.http.post<Task>(`${this.domain}/api/tasks`,newTask).map(res=>res);
  };
  deleteTasks(id){
    return this.http.delete<Task>(`${this.domain}/api/tasks/${id}`).map(res=>res);
  };  
  updateTasks(newTask){
    return this.http.put(`${this.domain}/api/tasks/${newTask._id}`,newTask);
  };
}



